.contact-preview {
    display: inline-flex;
    align-items: center;
    background: #f9ed69;
    border-radius: 12px;
    width: 260px;

    a {
        display: inherit;
        align-items: inherit;
    }

    img {
        margin-left: 8px;
        height: 60px;
    }

    span {
        margin-left: 8px;
        text-align: left;
        width: 138px;
        font-weight: 600;
    }
}